export const getPublications = (queryObj) => async (dispatch) => {
    dispatch({ type: ActionTypes.BALANCE_FILE_REQUEST, payload: { fileId } });

    try {
        // const response = await api.files.balance(fileId);
        const response = await api.files.getDelayedMock({
            status: 1
        });

        dispatch({ type: ActionTypes.BALANCE_FILE_SUCCESS, payload: { fileId } });
    } catch (error) {
        dispatch({ type: ActionTypes.BALANCE_FILE_FAIL, payload: { fileId, error } });
    }
}



